export default {
  props: {
    visible: {
      type: Boolean,
      default: false,
      required: true,
    },
    title: {
      type: String,
      default: '提示',
    },
    placeholder: {
      type: String,
      default: '请输入内容',
    },
    mainColor: {
      type: String,
      default: '#e74a39',
    },
    defaultValue: {
      type: String,
      default: '',
    },
    inputStyle: {
      type: String,
      default: '',
    },
    // 是否多行输入的textarea
    isMutipleLine: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
      value: '',
    }
  },
  watch: {
    visible(val) {
      if(val) {
        this.value = this.defaultValue
      }
    }
  },
  mounted() {
    this.value = this.defaultValue === 'true' ? '' : this.defaultValue
  },
  methods: {
    close() {
      this.$emit('update:visible', false)
    },
    confirm() {
      this.$emit('confirm', this.value)
      this.value = ''
    },
  }
}
